/* _print.scss */
/* ==========================================================================
   Print styles.
   Inlined to avoid the additional HTTP request:
   http://www.phpied.com/delay-loading-your-print-css/
   ========================================================================== */
@keyframes scale-star-compact {
  0% {
    background-size: 1.0625rem;
  }
  100% {
    background-size: 350%;
  }
}
@media print {
  *,
  *:before,
  *:after {
    background: transparent !important;
    color: #000 !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }
  * {
    font-size: 10pt !important;
    line-height: 16pt !important;
  }
  table {
    max-width: 100% !important;
    border: 1px solid #000 !important;
    table-layout: fixed !important;
  }
  table * {
    max-width: 100% !important;
    border-color: #000 !important;
  }
  table caption {
    border-width: 1px 1px 0px 1px;
    border-style: solid;
    border-color: #000;
  }
  table thead {
    display: table-header-group !important;
    break-inside: auto;
  }
  table tfoot {
    display: table-row-group !important;
  }
  table tr {
    border-bottom: 1px solid #ebebeb !important;
    page-break-inside: avoid;
  }
  table th {
    text-align: left;
  }
  table th:not(:last-child) {
    border-right: 1px solid #000;
  }
  table td:not(:last-child) {
    border-right: 1px solid #000;
  }
  table span {
    display: inline-block !important;
    max-width: 100% !important;
  }
  html,
  body {
    font-size: 10pt;
    line-height: 16pt;
  }
  body {
    padding: 1em;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
    padding: 0;
    text-indent: 0 !important;
    margin-bottom: 0.9375rem;
    margin-top: 2.1875rem;
    display: block !important;
    width: 100%;
    page-break-after: avoid;
    break-inside: avoid;
  }
  h1,
  #page-title,
  h1 *,
  #page-title * {
    font-weight: bold;
    font-size: 22pt !important;
    line-height: 28pt !important;
  }
  h2,
  h2 * {
    font-weight: bold;
    font-size: 18pt !important;
    line-height: 24pt !important;
  }
  h3,
  h3 * {
    font-weight: bold;
    font-size: 16pt !important;
    line-height: 20pt !important;
  }
  h4,
  h4 * {
    font-weight: bold;
    font-size: 14pt !important;
    line-height: 18pt !important;
  }
  h5,
  h5 * {
    font-weight: bold;
    font-size: 12pt !important;
    line-height: 16pt !important;
  }
  h6,
  h6 * {
    font-weight: bold;
  }
  strong {
    font-weight: 700;
  }
  a,
  a:visited {
    text-decoration: underline;
  }
  p {
    orphans: 3;
    widows: 3;
  }
  img {
    page-break-inside: avoid;
    max-width: 100% !important;
  }
  pre,
  blockquote {
    font-weight: 700 !important;
    font-style: italic;
    margin-top: 0.9375rem !important;
    margin-bottom: 0.9375rem !important;
    page-break-inside: avoid;
    font-size: 12pt;
  }
  pre *,
  blockquote * {
    font-size: 12pt;
  }
  form {
    display: block !important;
  }
}
@media print {
  .no-print {
    display: none !important;
  }
  .dialog-off-canvas-main-canvas {
    display: block !important;
    padding-top: 0 !important;
    min-height: 0 !important;
  }
  .container,
  .region,
  .block {
    margin: 0 !important;
    padding: 0 !important;
    display: block !important;
    max-width: none !important;
  }
  .table-container {
    position: static !important;
    overflow: visible !important;
    max-width: 100% !important;
  }
  .section-content {
    clear: both;
    padding-top: 2.1875rem !important;
  }
}
@media print {
  .block-system-breadcrumb-block {
    display: none !important;
  }
  .region--sidebar-first .block-menu {
    display: none !important;
  }
  .footer {
    display: none !important;
  }
  .pager {
    display: none !important;
  }
  .more-link {
    display: none !important;
  }
  .field--name-body img {
    display: none !important;
  }
  .mm-page {
    padding-top: 0 !important;
  }
}
@media print {
  .iframe-container {
    display: none !important;
  }
  .video-embed-field-responsive-video {
    display: none !important;
  }
  .embedded-entity {
    display: none !important;
  }
  .template--fancy-list .item-number {
    font-size: 36pt !important;
    line-height: 36pt !important;
  }
}
@media print {
  .template--accordion {
    margin-bottom: 0 !important;
    border-bottom: 0 !important;
  }
  .template--accordion > h2 {
    padding: 0 !important;
    margin-bottom: 0.9375rem !important;
    margin-top: 2.1875rem !important;
    border-top: 0 !important;
    border-bottom: 0 !important;
  }
  .template--accordion > h2:focus {
    border-top: 0 !important;
    border-bottom: 0 !important;
  }
  .template--accordion > h2::after {
    display: none !important;
  }
  .template--accordion .accordion-content {
    position: static !important;
    width: auto !important;
    height: auto !important;
    margin: 0 !important;
    overflow: visible !important;
    margin-bottom: 0 !important;
    padding: 0 !important;
  }
}
@media print {
  .block-system-branding-block {
    display: block !important;
  }
  .block-system-branding-block .site-logo {
    display: block;
    position: relative;
    width: 100%;
  }
  .block-system-branding-block .site-logo::before {
    display: block;
  }
  .block-system-branding-block .site-logo .logo--default {
    display: none !important;
  }
  .block-system-branding-block .site-logo .logo--print {
    display: block !important;
    width: 300px !important;
    height: auto !important;
  }
  .block-system-branding-block .site-name {
    display: none !important;
  }
}
@media print {
  .block-local-tasks-block {
    display: none !important;
  }
  .messages {
    display: none !important;
  }
}
@media print {
  .masthead {
    padding-left: 0;
    padding-right: 0;
  }
  .region--masthead-primary {
    width: 100% !important;
    height: auto !important;
    padding-bottom: 2.1875rem !important;
    border-bottom: 0 !important;
    padding-top: 1.25rem !important;
  }
  .region--masthead-primary .region__inner {
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
  .masthead__primary-right {
    display: none;
  }
  .menu-toggle-wrapper {
    display: none !important;
  }
}
@media print {
  .search-toggle-wrapper {
    display: none !important;
  }
  .search-overlay {
    display: none !important;
  }
  .block-facets {
    display: none !important;
  }
  .search-view .view-header {
    display: none !important;
  }
  .search-view .view-filters {
    display: none !important;
  }
}